.navigation{
   position: fixed;
   background-color: rgb(75, 73, 73);
   width:100%;
   height: 80px;
   display: flex;
   align-items: center;
   column-gap: 40px;
   justify-content: center;
   a{
      color: white;
      font-size: 20px;
      text-decoration: none;
      transition: 0.4s;
   &:hover{
      transform: scale(1.05);
   }
   }
}