.django{
   display: flex;
   flex-direction: column;
   align-items: flex-start;
   h1{
      text-align: center;
   }
   summary{
      user-select: none;
      cursor: pointer;
      font-size: 20px;
   }
   table{
      border-collapse: collapse;
   }
   td{
      padding: 10px;
      border: solid black 1px;
      color: white;
   }
}